  flex-wrap: wrap;
  padding-top: 23px;
}

.play-btn-two .icon {
  width: 52px;
  height: 52px;
  background: #fd4956;
  color: #272b41;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 300px;
  position: relative;
  margin-right: 40px;
  transition: all 0.3s ease;
}

.play-btn-two .icon i {
  color: #fff;
}

.play-btn-two .icon:before {
  position: absolute;
  content: "";
  width: 78px;
  height: 78px;
  border-radius: 300px;
  background: rgba(253, 73, 86, 0.1);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
  animation: wave-effect 1.5s alternate infinite;
}

.play-btn-two .icon:after {
  position: absolute;
  content: "";
  width: 110px;
  height: 110px;
  border-radius: 300px;
  background: rgba(253, 73, 86, 0.1);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: wave-effect 1.5s alternate infinite;
  animation-delay: 0.5s;
}

.play-btn-two span {
  color: #272b41;
}

.play-btn-two:hover {
  color: #333;
}

.play-btn-two:hover .icon:before {
  width: 88px;
  height: 88px;
}

@-webkit-keyframes wave-effect {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    visibility: visible;
  }
  100% {
    width: 7.33333rem;
    height: 7.33333rem;
    opacity: 0.8;
    visibility: hidden;
  }
}

@-moz-keyframes wave-effect {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    visibility: visible;
  }
  100% {
    width: 110px;
    height: 110px;
    opacity: 0.8;
    visibility: hidden;
  }
}

@-o-keyframes wave-effect {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    visibility: visible;
  }
  100% {
    width: 110px;
    height: 110px;
    opacity: 0.8;
    visibility: hidden;
  }
}

@keyframes wave-effect {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    visibility: visible;
  }
  100% {
    width: 110px;
    height: 110px;
    opacity: 0.8;
    visibility: hidden;
  }
}

